import "./styles.css";

//initial todos
let todos = [
  {
    todoID: 0,
    todoText: "Finish Homework",
    todoComplete: false
  },
  {
    todoID: 1,
    todoText: "Walk the dog it is fun",
    todoComplete: true
  },
  {
    todoID: 2,
    todoText: "Clean my room",
    todoComplete: false
  }
];

const addButton = document.querySelector("#addButton");
const todoInput = document.querySelector("#todoInput");
const todoUL = document.querySelector(".todoList");
const clearButton = document.querySelector("#clearDone");

//event listeners

//add todo on plus button click
addButton.addEventListener("click", () => {
  addTodo(todoInput.value);
  todoInput.value = "";
});

//add todo on enter
todoInput.addEventListener("keydown", function (e) {
  if (e.code === "Enter") {
    addTodo(todoInput.value);
    todoInput.value = "";
  }
});

let timer;

//complete and delete
todoUL.addEventListener(
  "click",
  (e) => {
    if (e.detail === 1) {
      timer = setTimeout(() => {
        //click to complete
        if (e.target.tagName === "LI") {
          const clickedID = parseInt(e.target.dataset.todoid, 10);

          todos = todos.map((todo) => {
            if (todo.todoID === clickedID) {
              return { ...todo, todoComplete: !todo.todoComplete };
            }

            return todo;
          });

          renderTodos(todos);
        }

        //click garbage to remove
        if (e.target.tagName === "SPAN" || e.target.tagName === "I") {
          let clickedID;

          //deal with the way the HTML is
          clickedID = Number(e.target.parentElement.dataset.todoid);
          if (e.target.tagName === "I")
            clickedID = Number(
              e.target.parentElement.parentElement.dataset.todoid
            );

          //filter out the delted todo
          todos = todos.filter((todo) => todo.todoID !== clickedID);

          renderTodos(todos);
        }
      }, 200);
    }
  },
  false
);

todoUL.addEventListener("dblclick", (event) => {
  clearTimeout(timer);
  console.log("dblclick");
});

//clear done button
clearButton.addEventListener("click", () => {
  todos = todos.filter((todo) => todo.todoComplete !== true);
  renderTodos(todos);
});

function addTodo(todoText) {
  const newTodo = {
    todoID: todos.length + 1,
    todoText,
    todoComplete: false
  };

  todos = [...todos, newTodo];

  renderTodos(todos);
}

function renderTodos(todos) {
  console.log(todos);
  const todoList = document.querySelector(".todoList");
  todoList.innerHTML = "";

  todos.forEach((todo) => {
    const todoClass = todo.todoComplete ? "done" : "";

    const todoHTML = `<li data-todoId='${todo.todoID}' class="${todoClass}">
                        ${todo.todoText}<span> <i class="fa fa-trash"></i></span>
                      </li>`;

    todoList.insertAdjacentHTML("beforeEnd", todoHTML);
  });

  const pending = todos.filter((todo) => todo.todoComplete !== true).length;
  const pendingSpan = document.querySelector("#pending");

  pendingSpan.innerHTML = `You have ${pending} pending tasks.`;
}

//init todos
renderTodos(todos);
